<template>
	<swiper ref="mySwiper" class="swiper" :options="swiperOptions">
		<swiper-slide v-for="index in 6" :key="index">
			<v-card
				class="mx-auto"
				min-width="300"
				elevation="5"
				align="left"
				light>
				<v-img
					class="white--text align-end"
					height="300px"
					:src="require('../../assets/brawstars/season/season-2.jpg')">
					<v-card-title style="background-color: rgba(60, 60, 60, 0.6);">
						Top {{ index }} Australian beaches
					</v-card-title>
				</v-img>

				<v-card-subtitle class="pb-0">Number {{ index }}</v-card-subtitle>

				<v-card-text class="">
					<div>Whitehaven Beach</div>
					<div>Whitsunday Island, Whitsunday Islands</div>
				</v-card-text>

				<v-card-actions>
					<v-btn
						text
						color="deep-purple accent-4">
						Read
					</v-btn>
					<v-btn
						text
						color="deep-purple accent-4">
						Bookmark
					</v-btn>
					<v-spacer></v-spacer>
					<v-btn icon>
						<v-icon>mdi-heart</v-icon>
					</v-btn>
					<v-btn icon>
						<v-icon>mdi-share-variant</v-icon>
					</v-btn>
				</v-card-actions>
			</v-card>
		</swiper-slide>
		<div class="swiper-button-prev" slot="button-prev" style="color: grey;"></div>
		<div class="swiper-button-next" slot="button-next" style="color: grey;"></div>
		<!-- <div class="swiper-pagination" slot="pagination"></div> -->
	</swiper>
</template>

<style scoped>
	.swiper {
		height: 100%;
		padding: 20px;
		width: 100%;
	}

	.swiper-slide {
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		font-weight: bold;
		font-size: 2em;
		/*background-color: grey;*/
	}
</style>

<script>
	import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
	// import 'swiper/swiper-bundle.css'
	import "swiper/css/swiper.css"

    export default {
        name: 'BrawstarsSwiper',

		components: {
			Swiper,
			SwiperSlide
		},

        data: () => ({
			swiperOptions: {
				slidesPerView: 3,
				spaceBetween: 30,
				// slidesOffsetBefore: 50,
				// slidesOffsetAfter: 50,
				// centeredSlides: true,
				// slidesPerGroup: 2,
				height: '100px',
				
				// Responsive breakpoints
				breakpoints: {
					// when window width is >= 320px
					320: {
						slidesPerView: 1,
						spaceBetween: 10
					},
					// when window width is >= 480px
					480: {
						slidesPerView: 1,
						spaceBetween: 10
					},
					// when window width is >= 750px
					750: {
						slidesPerView: 3,
						spaceBetween: 30
					}
				},
				navigation: {
					nextEl: '.swiper-button-next',
					prevEl: '.swiper-button-prev'
				}
				// Some Swiper option/callback...
			}
        }),

		computed: {
			swiper() {
				return this.$refs.mySwiper.$swiper
			}
		},

		mounted() {
			console.log('Current Swiper instance object', this.swiper)
			this.swiper.slideTo(3, 1000, false)
		}
    }
</script>