            GridItem: VueGridLayout.GridItem
        },

        data: () => ({
            layout: [
                {"x":0,"y":0,"w":5,"h":10,"i":"0"},
                {"x":5,"y":0,"w":5,"h":5,"i":"1"},
                {"x":5,"y":5,"w":5,"h":5,"i":"2"},
            ],
            dataImg: [
                {
                    height: '450',
                    title: 'honey mustard chicken',
                },
                {
                    height: '220',
                    title: 'creamy tomato chicken',
                },
                {
                    height: '220',
                    title: 'tasty tuna',
                },
            ] 
        }),
    }
</script>