<template>
	<div class="" fluid>
		<transition name="fade">
            <div class="loadingVue" v-if="getStatusLoadingCola == true">
                <scale-loader :loading="getStatusLoadingCola" :color="color" :height="height" :width="width" class=""></scale-loader>
            </div>
        </transition>
		<NavigationBar />
		<SectionHero />
	</div>
</template>

<style scoped>
    .loadingVue {
        position: absolute;
        z-index: 100;
        background: #0161b8;
        height: 100vh;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 1s;
    }
    .fade-enter-active, .fade-leave-active {
        transition: opacity 1s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
</style>

<script>
	import ScaleLoader from 'vue-spinner/src/ScaleLoader.vue'
	import { mapGetters, mapActions } from 'vuex'

	export default {
		name: 'CocaCola',

		components: {
			ScaleLoader,
			NavigationBar: () => import( /* webpackChunkName: "alert" */ '@/components/cocacola/NavigationBar.vue'),
			SectionHero: () => import( /* webpackChunkName: "alert" */ '@/components/cocacola/SectionHero.vue'),
		},

		computed: {
            ...mapGetters({
                getStatusLoadingCola:'getStatusLoadingCola',
            }),
        },

		created() {
            this.setStatusLoadingCola({
                status: false
            })
        },

		beforeDestroy () {
            this.setStatusLoadingCola({
                status: true
            })
        },

		data: () => ({
            color: '#ffffff',
            height: '45px',
            width: '14px', 
        }),

        methods: {
            ...mapActions({
                setStatusLoadingCola: 'setStatusLoadingCola',
            }),
        },
	}
</script>