<template>
	<v-parallax
		dark
		:src="require('../../assets/brawstars/bg_gamesocial_brawlstars.jpg')"
		:height="heightParalax"
		id="game-preview">
		<v-row
			align="center">
			<v-col col="12" md="7">
				<v-img
				:src="require('../../assets/brawstars/mockup-game-brawstart-1.png')"
				class="my-3"
				contain
				height=""/>
			</v-col>
			<v-col col="12" md="5">
				<div class="" style="background-color: black;">
					<p class="title-gameplay">3v3 and Battle Royale</p>
				</div>
				<div class="" style="background-color: black;margin-top: 1.5em;">
					<p class="descript-gameplay">
						Time to BRAWL! Team up with your friends and get ready for epic multiplayer MAYHEM! Brawl Stars is the newest game from the makers of Clash of Clans and Clash Royale. Jump into your favorite game mode and play quick matches with your friends. Shoot 'em up, blow 'em up, punch 'em out and win the BRAWL.
					</p>
				</div>
			</v-col>
		</v-row>
	</v-parallax>
</template>


<style scoped>
	.title-gameplay {
		font-size: 3em;
		font-weight: bold;
		text-transform: uppercase;
		background-color: white;
		color: black;
		text-align: center;
		transform: skewY(-3deg);
	}
	.descript-gameplay {
		background-color: white;
		color: black;
		font-size: 1.2em;
		font-weight: bold;
		padding: 10px;
		transform: skewY(-3deg);
	}
</style>

<script>
	export default {
		name: 'GamePreview',

		created() {
			this.$nextTick(() => {
				window.addEventListener('resize', this.onResize);
			})
		},

		beforeDestroy() { 
			window.removeEventListener('resize', this.onResize); 
		},

		watch: {
			windowWidth(newWidth, oldWidth) {
				this.oldWidth = oldWidth
				this.newWidth = newWidth
				// this.txt = `it changed to ${newWidth} from ${oldWidth}`;
				// this.widthDialog = this.windowWidth < 768 ? '95%' : '90%';
			}
		},

		data: () => ({
			windowWidth: window.innerWidth,
			oldWidth: '',
			newWidth: '',
			heightParalax: '700',
		}),

		methods: { 
			onResize() {
				this.windowWidth = window.innerWidth
				if (this.windowWidth < 600) {
					this.heightParalax = 900
				}
				else {
					this.heightParalax = 700
				}
			},
        },
	}
</script>