<template>
	<v-parallax
		dark
		:src="require('../../assets/brawstars/section-hero-2.jpg')"
		:height="heightParalax"
		id="section-hero-brawstar">
		<v-row class="d-flex align-center justify-center" style="min-height: 90vh;">

			<v-col class="d-flex flex-column justify-center align-center" cols="12" md="5">
				<v-img
					:src="require('../../assets/brawstars/brawl-stars-text.png')"
					class="my-3"
					contain
					height="275"/>
				<div class="d-flex justify-center align-center">
					<v-btn x-large color="" dark>
						<v-icon class="mr-2">mdi-google-play</v-icon>
						<b>Play Store</b>
					</v-btn>
					<v-btn x-large color="" dark class="ma-2">
						<v-icon class="mr-2">mdi-apple</v-icon>
						<b>Apple Store</b>
					</v-btn>
				</div>
			</v-col>

			<v-col class="d-none d-md-flex" cols="12" md="5">
				<BrawstarsLogo></BrawstarsLogo>
			</v-col>

		</v-row>
	</v-parallax>
</template>


<style scoped>
	.bg-orange-2 {
		/*background: radial-gradient(#ffe333, #febd01, #f87003);*/
		background-image: url('~@/assets/brawstars/section-hero-2.jpg');
		background-size: cover;
		background-position: center;
	}
	.char-bold-crimson {
		font-weight: bold;
		color: crimson;
	}.
	.section-hero-title {
		/*font-family: 'Gayathri', sans-serif;*/
	}
	.section-hero-subtitle {
		font-weight: bold;
		text-transform: uppercase;
		font-size: 1.2em;
		color: crimson;
	}
	@media only screen and (max-width: 960px) {

	}
</style>

<script>
	import BrawstarsLogo from '@/components/brawstars/BrawstarsLogo.vue'

	export default {
		name: 'SectionHero',

		components: {
			BrawstarsLogo
		},

		created() {
			this.$nextTick(() => {
				window.addEventListener('resize', this.onResize);
			})
		},

		beforeDestroy() { 
			window.removeEventListener('resize', this.onResize); 
		},

		watch: {
			windowWidth(newWidth, oldWidth) {
				this.oldWidth = oldWidth
				this.newWidth = newWidth
				// this.txt = `it changed to ${newWidth} from ${oldWidth}`;
				// this.widthDialog = this.windowWidth < 768 ? '95%' : '90%';
			}
		},

		data: () => ({
			heightParalax: '700',
		}),

		methods: { 
			onResize() {
				this.windowWidth = window.innerWidth
				if (this.windowWidth < 600) {
					this.heightParalax = 900
				}
				else {
					this.heightParalax = 700
				}
			},
        },
	}
</script>