<template>
	<v-parallax
		dark
		:src="require('../../assets/brawstars/bg_gamesocial_brawlstars.jpg')"
		:height="heightParalax"
		id="game-character">
		<!-- <div class=""> -->
		<v-row
			align="center"
			class="d-flex justify-center">
			<v-col col="12" md="5" class="d-none d-sm-none d-md-flex justify-center">
				<v-img
					:src="require('../../assets/brawstars/character/' + curentChracter.img )"
					class="my-3"
					contain
					height="600"/>
			</v-col>
			<v-col col="12" md="5">
				<div class="">
					<div class="box-container-character-name">
						<div class="box-character-name" 
							:style="{ 'background-color': curentChracter.color }"></div>
					</div>
					<p class="mb-3 text-uppercase font-text-name-character text-character-name-1">
						{{ curentChracter.name }}
					</p>
					<p class="text-uppercase font-text-name-character text-sub-character-name-1">
						{{ curentChracter.charType }}
					</p>
					<div class="box-container-descript-2">
						<div class="box-descript-2">
							<p class="text-uppercase box-char-descript-title">
								{{ curentChracter.subDescript }}
							</p>
							{{ curentChracter.descript }}
						</div>
					</div>
					<v-row class="flex-md-row flex-sm-row">
						<v-col col="12" md="4" sm="4" v-for="char in characters" :key="char.name" @click="changeCharacter(char)">
							<div class="d-flex justify-center align-center box-select-character text-uppercase char-selection"
								:class="{ 'box-container-descript-2': curentChracter.name == char.name }"
								:style="{ 'background-color': char.color}">
								<span class="box-char-name">
									{{ char.name }}
								</span>
							</div>
						</v-col>
					</v-row>
				</div>
			</v-col>
		</v-row>
		<!-- </div> -->
	</v-parallax>
</template>

<style scoped>
	.char-selection .active2 {
		background-color: pink;
		color: red;
	}
	.box-select-character {
		margin-top: 1em;
		color: black;
		height: 8em;
	}
	.box-container-descript-2 {
		filter: drop-shadow(10px 10px 0px rgba(0,0,0,1));
	}
	.char-selection {
		cursor: pointer;
	}
	.char-selection:hover {
		transition: .3s;
		filter: drop-shadow(10px 10px 0px rgba(0,0,0,1));
	}
	.box-descript-2 {
		margin-top: 3em;
		color: black;
		padding: 2em 2em 2em 2em;
		width: 100%;
		height: 100%;
		background-color: white;
		/*clip-path: polygon(0px 28px, 100% 14%, 100% 100%, 0% 100%)*/
		/*clip-path: polygon(0px 28px, 600px 14%, 600px 400px, 0px 400px);*/
	}
	.box-container-character-name{
		filter: drop-shadow(10px 10px 0px rgba(0,0,0,1));
    }
	.box-character-name {
		width: 20em;
		height: 15em;
		/*background-color: #f8c820;*/
		clip-path: polygon(0 16%, 100% 0, 100% 63%, 0px 62%)
	}
	.font-text-name-character {
		font-weight: bold;
		transform: skewY(-5deg);
	}
	.text-character-name-1 {
		text-shadow: black -2px 8px 0px;
		letter-spacing: -4px;
		font-size: 6em;
		margin: -2.7em 0 0 .3em;
	}
	.text-sub-character-name-1 {
		font-size: 2em;
		margin: -1.8em 0 0 4em;
		text-shadow: black -1px 4px 0px;
	}
	.box-char-name {
		font-weight: bold;
		transform: skewY(-5deg);
		font-size: 2.5em;
		text-shadow: black -2px 4px 0px;
		color: white;
	}
	.box-char-descript-title {
		font-weight: bold;
		font-size: 2em;
		transform: skewY(-2deg);
	}
	@media only screen and (max-width: 960px) {

	}
</style>

<script>
    export default {
        name: 'BrawstarsCharacter',

        data: () => ({
			kotak: 'mdi-square-rounded', 
			isActive: true,
			windowWidth: window.innerWidth,
			heightParalax: '700',
			curentChracter: {
				name: 'crow',
				charType: 'legendary',
				color: '#f8c820',
				subDescript:'toxic assasin',
				descript: 'Lorem ipsum, dolor sit amet consectetur adipisicing elit. Dolorem nulla nobis suscipit aperiam quisquam illo doloribus dolorum nisi, totam, ratione.',
				icon: '',
				img: 'char-crow-modif.png',
			},
			characters: [
				{
					name: 'crow',
					charType: 'legendary',
					color: '#f8c820',
					descript: 'Lorem ipsum, dolor sit amet consectetur adipisicing elit. Dolorem nulla nobis suscipit aperiam quisquam illo doloribus dolorum nisi, totam, ratione.',
					subDescript:'toxic assassin',
					icon: '',
					img: 'char-crow-modif.png',
				},
				{
					name: 'shelly',
					charType: 'normal',
					color: 'grey',
					descript: 'Lorem ipsum, dolor sit amet consectetur adipisicing elit. Dolorem nulla nobis suscipit aperiam quisquam illo doloribus dolorum nisi, totam, ratione. Lorem ipsum, dolor sit amet consectetur adipisicing elit. Dolorem nulla nobis suscipit aperiam quisquam illo doloribus dolorum nisi, totam, ratione.',
					subDescript:'fighter',
					icon: '',
					img: 'char-shelly-modif.png',
				},
				{
					name: 'penny',
					charType: 'rare',
					color: 'lightblue',
					descript: 'Lorem ipsum, dolor sit amet consectetur adipisicing elit. Dolorem nulla nobis suscipit aperiam quisquam illo doloribus dolorum nisi, totam, ratione. Lorem ipsum, dolor sit amet consectetur adipisicing elit.',
					subDescript:'sharpshooter',
					icon: '',
					img: 'char-penny-modif.png',
				}
			]
        }),

		created() {
			this.$nextTick(() => {
				window.addEventListener('resize', this.onResize);
			})
		},

		beforeDestroy() { 
			window.removeEventListener('resize', this.onResize); 
		},

		watch: {
			windowWidth(newWidth, oldWidth) {
				this.oldWidth = oldWidth
				this.newWidth = newWidth
				// this.txt = `it changed to ${newWidth} from ${oldWidth}`;
				// this.widthDialog = this.windowWidth < 768 ? '95%' : '90%';
			}
		},

        methods: { 
			changeCharacter(data){
				this.curentChracter.name 		= data.name
				this.curentChracter.charType 	= data.charType
				this.curentChracter.color 		= data.color
				this.curentChracter.descript 	= data.descript
				this.curentChracter.subDescript 	= data.subDescript
				this.curentChracter.icon 		= data.icon
				this.curentChracter.img 		= data.img
			},
			onResize() {
				this.windowWidth = window.innerWidth
				if (this.windowWidth < 600) {
					this.heightParalax = 900
				}
				else {
					this.heightParalax = 700
				}
			},
        },
    }
</script>