<template>
	<div class="">
		<v-navigation-drawer
            class="hidden-md-and-up"
            v-model="drawer"
            app
            clipped
            disable-resize-watcher
            elevate-on-scroll
            color="">
            <v-list dense>
                <v-list-item link v-for="item in items" :key="item.title"
                    @click="$vuetify.goTo(item.link, options)">
                    <v-list-item-action>
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title style="text-transform: uppercase;">{{ item.title }}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>

        <v-app-bar
            app
            clipped-left
            flat
            :color="colorFont">
            <v-app-bar-nav-icon @click.stop="drawer = !drawer" class="hidden-md-and-up"></v-app-bar-nav-icon>
            <v-toolbar-title>
                <b style="text-transform: uppercase;">{{ currentRouteName }}</b>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items class="hidden-sm-and-down">
                <v-btn v-for="item in items" :key="item.title" :color="colorFont" depressed 
                    @click="$vuetify.goTo(item.link, options)">
                    {{ item.title }}
                </v-btn>
            </v-toolbar-items>
        </v-app-bar>
	</div>
    
</template>


<script>
    export default {
		name: 'NavigationBar',

		computed: {
			currentRouteName() {
				return this.$route.name
			},
            target () {
                return '#game-preview'
            },
            options () {
                return {
                    duration: this.duration,
                    offset: this.offset,
                    easing: this.easing,
                }
            },
		},

		created () {
			this.$vuetify.theme.dark = true
		},

		props: {
			source: String,
		},

		components: {
		// HelloWorld,
		},

        data: () => ({
			fab: false,
			transition: 'slide-x-reverse-transition',
			drawer: false,
            duration: 300,
            offset: 0,
            easing: 'easeInOutCubic',
			items: [
                { title: 'Brawstars', icon: 'mdi-home', link: '#section-hero-brawstar', color:'#febd01'},
                { title: 'Gameplay', icon: 'mdi-play', link: '#game-preview', color:'#febd01'},
                { title: 'Mode', icon: 'mdi-gamepad-square', link: '#game-mode', color:'#febd01'},
                { title: 'Character', icon: 'mdi-rocket-launch', link: '#game-character', color:'#febd01'},
                { title: 'Season', icon: 'mdi-calendar-month', link: '#game-season', color:'#febd01'},
			],
			route_now: '',
			colorFont: '',
        }),

        methods: {
        }
    }
</script>